import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'maptokeys'
})
export class MaptoKeysPipe implements PipeTransform {

  transform(value, args:string[]) : any {
    let keys = [];
    for (let key in value) {
      keys.push({key: key, value: value[key]});
    }
    return keys;
  }
}



